import { useEffect, useState } from "react";
import { Button, Modal, Input, Form } from "antd";
import "./index.css";

function UnitTest(props) {
  const [visible, setVisible] = useState(false);
  const [form] = Form.useForm();

  useEffect(() => {
    console.log('url:', window.location.href)
    const apiKey = localStorage.getItem('chatgpt_api_key');
    if ( !apiKey ) {
      setVisible(true);
    } else {
      window.CHATGPT_API_KEY = apiKey;
    }
  }, []);


  const handleOk = async () => {
    const data = await form.validateFields();
    console.log('', data.target);
    window.CHATGPT_API_KEY = data.target;
    localStorage.setItem('chatgpt_api_key', data.target);

    setVisible(false);
    form.resetFields();
  };

  const handleCancel = () => {
    setVisible(false);
    form.resetFields();
  };

  return (
    <div className="chatgpt_unit_test">
      <Modal
        title={' API Key'}
        visible={visible}
        onOk={handleOk}
        onCancel={handleCancel}
        okText={''}
        footer={<Button type="primary" onClick={handleOk}></Button>}
      >
        <Form
          form={form}
          initialValues={{
            target: ``,
          }}
        >
          <Form.Item name="target">
            <Input placeholder=" API Key" />
          </Form.Item>
        </Form>
      </Modal>
    </div>
  );
}

export default UnitTest;
